.board {
  font-size: 0;
}

.info {
  margin-bottom: 20px;
  font-size: 20px;
  font-weight: bold;
}

.popup {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.92);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  text-align: center;
}

.cell {
  width: 20px;
  height: 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: inline-block;
}

.cell_view_empty {
  border: 1px solid #000;
}

.cell_view_snake {
  background: #000;
}

.cell_view_treat {
  background: royalblue;
}


